<script>
import HeaderComponent from "@/components/Header1/header1.vue";

export default {
  name: "viewSignOffDetails-component",
  components: { HeaderComponent },
  data() {
    return {
      projectInfo: {},
      reason: [],
      signOffDetail: [],
      signOffHead: [],
      workPerson: [],
    };
  },
  computed: {
    waitStaff() {
      return this.signOffHead
        .map((x) => `${x.name} (${x.processor_Empno}/${x.ext_No}/${x.e_Mail})`)
        .join("、");
    },
  },
  methods: {
    async urgentRepairDownloadPdf(fileId, fileName) {
      try {
        const res = await this.$store.dispatch(
          "urgentRepairDownloadPdf",
          fileId
        );
        const href = URL.createObjectURL(res.data);
        const link = document.createElement("a");
        link.href = href;
        link.setAttribute("download", `${fileName}.pdf`);
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        URL.revokeObjectURL(href);
      } catch (err) {
        console.log(err);
      }
    },
  },
  mounted() {
    if (this.$route.query.data) this.$root.overlay = false;
    this.projectInfo = JSON.parse(this.$route.query.data).project_Info;
    this.reason = JSON.parse(this.$route.query.data).reason;
    this.signOffDetail = JSON.parse(this.$route.query.data).signoff_Detail;
    if (JSON.parse(this.$route.query.data).signoff_Head.length) {
      this.signOffHead = JSON.parse(this.$route.query.data).signoff_Head;
    }
    this.workPerson = JSON.parse(this.$route.query.data).work_Person;

    this.$store.commit("HEADER_HANDLER", false);
    this.$store.commit("BREADCRUMB_HANDLER", false);
    this.$root.$children[0].sidebarSwitch = false;
  },
};
</script>

<template src="./viewSignOffDetails.html"></template>
